// rxjs:reactive„Å™Âá¶ÁêÜ„ÇíÂÆüÁèæ„Åô„Çã„É©„Ç§„Éñ„É©„É™
import 'rxjs/add/operator/switchMap';
import { Component, OnInit } from '@angular/core';
// Params: „Éë„É©„É°„Éº„ÇøÁÆ°ÁêÜ„Ç™„Éñ„Ç∏„Çß„ÇØ„ÉàÔºà„Åì„Åì„Å´„Åä„ÅÑ„Å¶„ÅØ:id„Å®„Åã„Éªangular„Å´„Åä„ÅÑ„Å¶Ê±éÁî®ÁöÑÔºâ
// Router: „É´„Éº„ÉÜ„Ç£„É≥„Ç∞Â§âÊõ¥„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
// ActivatedRoute: Áèæ„É´„Éº„Éà„ÇíÂèñÂæó„ÉªÁÆ°ÁêÜ„Åô„Çã„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
import { Params, Router, ActivatedRoute } from '@angular/router';

import { Album } from './../class/album';
import { Music } from './../class/music';
import { AlbumService } from './../service/album.service';

@Component({
  selector: 'app-music-comp',
  templateUrl: './music-comp.component.html',
  styleUrls: ['./music-comp.component.css'],
  providers: [
    AlbumService
  ]
})
export class MusicCompComponent implements OnInit {

  // „É¨„É≥„ÉÄ„É™„É≥„Ç∞„Ç™„Éñ„Ç∏„Çß„ÇØ„Éà
  selectedAlbum: Album;
  musics: Music[];
  selectedMusic: Music;

  // „ÇØ„É©„ÇπÂàùÊúüÂåñ
  constructor(
    private _router: Router,
    private _route: ActivatedRoute,
    private _service: AlbumService
  ) { }

  // constructor„ÅÆ„ÅÇ„Å®„Å´ÂÆüË°å„Åï„Çå„Çã
  ngOnInit() {
    // localhost:4200/detail/[0-9]+
    this._route.params
      // switchMap„ÅØpromise„ÇíÂèó„ÅëÂèñ„Çã„Å®resolve„Åï„Çå„Åü„Çø„Ç§„Éü„É≥„Ç∞„Åß
      .switchMap((params: Params) => this._service.getAlbum(+params['id']))
      .subscribe((album: Album) => {
        this.selectedAlbum = album;
        this._service.getMusics(album.id)
          .then((musics: Music[]) => this.musics = musics);
      });
  }

  // „Ç≥„É≥„Éù„Éç„É≥„ÉàÂÜÖ„ÅßÂÆüË°å„Åï„Çå„ÇãÈñ¢Êï∞
  onSelect(selectedMusic: Music) {
    this.selectedMusic = selectedMusic;
  }
}



